














































import { Component, Vue } from "vue-property-decorator";
@Component({})
export default class menus extends Vue {
  title: any = [
    { name: "前端菜单", path: "menu/webtitle", url: "item/webitem" },
    { name: "数据结构与算法菜单", path: "menu/algtitle", url: "item/algitem" },
    { name: "后端菜单", path: "menu/servertitle", url: "item/serveritem" },
    { name: "设计模式菜单", path: "menu/modetitle", url: "item/modeitem" }
  ];
  path = null;
  created() {
    if (!this.path) {
      this.path = this.title[0];
    }
    this.fetchval();
    window.console.log(this.$store.state.env);
  }
  async fetchval() {
    const val = await this.$http.get(`${this.path!["path"]}`);
    this.value = val.data;
    this.loading = false;
  }
  //分类
  value = "";
  option = "";
  formLabelAlign = {
    name: "",
    objid: this.option,
    context: ""
  };
  //提交
  async add() {
    this.formLabelAlign["objid"] = this.option;
    const res = await this.$http.post(
      `${this.path!["url"]}`,
      this.formLabelAlign
    );
    if (res.status === 200) {
      this.$message({
        message: "恭喜你，这是一条成功消息",
        type: "success"
      });
      this.$router.push({
        path: "/menulist",
        query: { id: this.path!["url"] }
      });
    }
  }
  //切换分类
  change(item: any) {
    this.loading = true;
    this.path = item;
    this.fetchval();
    this.formLabelAlign = {
      name: "",
      objid: this.option,
      context: ""
    };
    this.option = "";
    this.fileList = [];
  }
  //loading
  loading = true;
  //上传
  fileList = [];
  handleRemove(file: any, fileList: any) {
    window.console.log(file, fileList);
  }
  handleExceed(files: any, fileList: any) {
    this.$message.warning(
      `当前限制选择 1 个文件，本次选择了 ${
        files.length
      } 个文件，共选择了 ${files.length + fileList.length} 个文件`
    );
  }
  beforeRemove(file: any, fileList: any) {
    return this.$confirm(`确定移除 ${file.name}？`);
  }
  handleAvatarSuccess(file: any) {
    window.console.log(file);
    this.formLabelAlign.context = file;
  }
}
